.cart {
  margin-top: 20px;
  display: flex;
  cursor: pointer;
  transform: translateX(-15px);
  align-items: center;
  border-right: 1px solid gray;
  border-left: 1px solid gray;
  padding: 3px 4px;

  div {
    margin-right: 1px;
    font-size: 20px;
    &:hover {
      color: gray;
    }
  }
  .a {
    display: flex;
    position: relative;

    .p {
      margin-top: -1rem;
      font-weight: 500;
    }
  }
  &:hover {
    color: gray;
  }
}

.fixed {
  width: 100%;
  position: fixed;
  top: 0;
  // transition: all 0.5s;
  z-index: 9;
}

header {
  background-color: #09072b;
  background: linear-gradient(#09072b, #010f18);
  background: linear-gradient(to right, #011c3a 28%, #000000 50%, #011c3a);
  color: #fff;
  z-index: 999;
  width: 100%;

  .header {
    height: 60px;
    // max-width: 1000px;
    margin: 0 auto;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    box-sizing: border-box;
  }

  .logo a {
    cursor: pointer;
  }

  nav {
    display: flex;
    justify-content: space-between;
    z-index: 9;
    ul {
      display: flex;
      justify-content: space-between;
      list-style: none;
      margin-left: 0px;

      .logo-mobile {
        display: none;
      }

      li {
        margin: 0 20px;
        padding: 0, 0px;
        display: inline-block;
        &:hover {
          transform: translateY(2px);
          transition-duration: 200ms;
        }

        a {
          color: #fff;
          text-decoration: none;

          &:hover {
            color: grey;
          }
        }
      }
    }
  }
  .activeLink {
    color: gray;
    transform: translateY(2px);
  }
  .btn {
    font-size: 0.7rem;
    font-weight: 400;
    padding: 6px 6px;
    margin: 0 0px 0 0;
    border: 1px solid transparent;
    border-radius: 3px;
    cursor: pointer;
    color: white;
    background-color: #0b5ed7;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s;
    width: 100%;
    &:hover {
      background-color: #0d6efd;
      transform: translateY(-8%);
    }
  }
  .rightNav {
    width: 25%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    z-index: 9;

    a {
      color: white;
      text-decoration: none;
      margin-right: 10px;
      &:hover {
        color: gray;
      }
    }
  }

  .headerRight {
    display: flex;

    .cart a {
      display: flex;
      color: #fff;
      position: relative;
      &:hover {
        color: gray;
      }
      p {
        position: absolute;
        top: -1rem;
        right: -1rem;
        font-weight: 500;
      }
    }

    span {
      margin: 0 5px;

      p {
        color: #fff;
      }
    }

    .links a {
      margin: 5px;
      color: #fff;
      &:hover {
        color: orangered;
      }
      &.active {
        color: var(--color-danger);
      }
    }
  }

  .menuIcon {
    cursor: pointer;
    display: none;
  }

  @media screen and (max-width: 800px) {
    nav {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 50%;
      height: 100vh;
      background-color: var(--dark-blue);
      padding: 1rem;
      z-index: 999;
      transform: translateX(-200%);
      transition: all 0.3s;

      .nav-wrapper {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.5);
        transform: translateX();
        transition: all 0.3s;
      }

      .show-nav-wrapper {
        transform: translateX(100%);
      }

      ul {
        display: block;
        .logo-mobile {
          display: flex;
          justify-content: space-between;
          align-items: center;
          > * {
            cursor: pointer;
          }
        }
        li {
          padding: 5px 0;
          border-bottom: 1px solid #333;
          a {
            display: block;
          }
        }
      }

      .headerRight {
        display: block;
        .cart {
          // display: block;
          // padding: 5px 0;
          border-bottom: 1px solid #333;
          // a {
          //   position: relative;
          //   p {
          //     position: absolute;
          //     top: -1rem;
          //     left: 5rem;
          //     font-weight: 500;
          //   }
          // }
        }

        .links {
          display: block;
          a {
            display: block;
            margin: 0;
            padding: 5px 0;
            border-bottom: 1px solid #333;
          }
        }
      }
    }
    .cart {
      display: block;
      padding: 5px 0;
      // border-bottom: 1px solid #333;
      a {
        color: #fff;
        position: relative;
        &:hover {
          color: orangered;
        }
        p {
          position: absolute;
          top: -1rem;
          left: 5rem;
          font-weight: 500;
          color: #fff;
        }
      }
    }
    .show-nav {
      transform: translateX(0);
    }
    .hide-nav {
      transform: translateX(-200%);
    }
    .menuIcon {
      display: flex;
      > * {
        margin-left: 2rem;
      }
    }
  }
}

.active {
  position: relative;
  color: var(--color-danger);
}

.active::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -3px;
  width: 100%;
  height: 2px;
  background-color: #fff;
}

.sign {
  cursor: pointer;
  &:hover {
    color: gray;
  }
}
